import axios from '@/utils/http' // httpaxios
// import Qs from 'qs'
const TaskCollection = {
  // 1
  getProject () {
    return axios({
      method: 'post',
      url: '/taskClaim/getProject'
      // data: params
    })
  },
  //  
  claim (params) {
    return axios({
      method: 'post',
      url: '/taskClaim/claim',
      data: params
    })
  },
  // 
  getClaimTaskList (params) {
    return axios({
      method: 'post',
      url: '/taskClaim/getClaimTaskList',
      data: params
    })
  }

}
export default TaskCollection



