import axios from '@/utils/http' // httpaxios
// import Qs from 'qs'
const yynl = {
  // 
  queryLanguageAbility (params) {
    return axios({
      method: 'post',
      url: '/resume/queryLanguageAbility',
      data: params
    })
  },
  // 
  saveLanguageAbility (params) {
    return axios({
      method: 'post',
      url: '/resume/saveLanguageAbility',
      data: params
    })
  },

  // 
  delLanguageAbility (params) {
    return axios({
      method: 'post',
      url: '/resume/delLanguageAbility',
      data: params
    })
  }

}
export default yynl



