<template>
<div class="box-table">
<!-- start-->
<el-form :inline="true" :model="quer" class="demo-form-inline">
  <el-form-item label="">
    <el-input v-model="quer.quTitle" placeholder=""></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="quer.projectName" placeholder=""></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="quer.quType" placeholder="">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
      <el-option label="" value="3"></el-option>
      <el-option label="" value="4"></el-option>
      <el-option label="" value="5"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit"></el-button>
  </el-form-item>
</el-form>
<!-- end-->
<!-- start-->
<el-row>
  <el-col :span="3"><div class="grid-content bg-purple"><el-button type="primary" @click="questAdd = true" icon="el-icon-edit"></el-button></div></el-col>
  <el-col :span="3"><div class="grid-content bg-purple"><el-button type="primary" @click="questAdd = true" icon="el-icon-edit-outline"></el-button></div></el-col>
  <el-col :span="1"><div class="grid-content bg-purple"><el-button type="primary" @click="delData" icon="el-icon-delete" ></el-button></div></el-col>
</el-row>
<!-- end-->
<!-- start-->
<el-row>
   <el-table :data="tableData" border style="width: 100%">
    <el-table-column  type="selection"  width="55"  align="center"></el-table-column>
    <el-table-column prop="quTitle" label="" width="250"></el-table-column>
    <el-table-column prop="quType" label="" width="245"></el-table-column>
    <el-table-column prop="projectName" label="" width="240"></el-table-column>
    <el-table-column prop="createUserName" label="" width="240"></el-table-column>
    <el-table-column prop="createDate" label="" width="240"></el-table-column>
  </el-table>
</el-row>
<el-row >
  <div class="block">
    <el-pagination background layout="total,prev, pager, next,jumper" :total="1000"></el-pagination>
  </div>
</el-row>
<!-- end-->
<!-- start-->
<el-dialog title="" :visible.sync="questAdd"  center>
<el-form  label-width="80px"  :model="quer" >
<el-form-item label=""  >
  <el-input v-model="quer.quTitle"  placeholder="" size="medium"  ></el-input>
</el-form-item>
<el-form-item label="">
    <el-select v-model="quer.quType" placeholder="" size="medium">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
      <el-option label="" value="3"></el-option>
      <el-option label="" value="4"></el-option>
      <el-option label="" value="5"></el-option>
    </el-select>
</el-form-item>
<el-form-item label="">
  <el-input v-model="quer.projectName" placeholder="" ></el-input>
</el-form-item>
<el-form-item>
  <el-button type="primary" @click="questAdd = false"  round></el-button>
</el-form-item>
</el-form>
</el-dialog>
<!-- end-->
</div>
</template>
<script>
export default {
  data () {
    return {
      quer: {
        quTitle: '',
        quType: '',
        projectName: ''
      },
      questAdd: false,
      tableData: [
        {
          quTitle: '1',
          quType: '',
          projectName: '',
          createUserName: 'admin',
          createDate: '2019-4-16'
        },
        {
          quTitle: '1',
          quType: '',
          projectName: '',
          createUserName: 'admin',
          createDate: '2019-4-16'
        },
        {
          quTitle: '1',
          quType: '',
          projectName: '',
          createUserName: 'admin',
          createDate: '2019-4-16'
        },
        {
          quTitle: '1',
          quType: '',
          projectName: '',
          createUserName: 'admin',
          createDate: '2019-4-16'
        }
      ]
    }
  },
  methods: {
    onSubmit () {
      console.log('submit')
    },
    delData () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.el-pagination{
  margin-top:20px;
  text-align: right;
}
</style>



