<template>
  <div class="box-table">
    <el-form ref="form" :model="form" label-width="80px" label-position="top">
      <el-row type="flex" justify="space-between">
        <el-col :span="3">
          <el-autocomplete class="input1" v-model="form.projectId" :fetch-suggestions="querySearch" placeholder=""
            :trigger-on-focus="false" @select="handleSelect"></el-autocomplete>
        </el-col>
        <el-col :span="3">
          <el-autocomplete class="input1" v-model="form.tid" placeholder="/" :trigger-on-focus="false"
            @select="handleSelect"></el-autocomplete>
        </el-col>
        <el-col :span="3">
          <el-autocomplete class="input1" v-model="form.tname" placeholder="/" :trigger-on-focus="false"
            @select="handleSelect"></el-autocomplete>
        </el-col>
        <el-col :span="3">
          <el-select class="select1" v-model="form.taskClass" clearable placeholder="">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <el-select class="select1" v-model="form.effectDegree" clearable placeholder="">
            <el-option v-for="item in options1" :key="item.value1" :label="item.label1" :value="item.value1">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <el-select class="select1" v-model="form.dependencyDegree" clearable placeholder="">
            <el-option v-for="item in options2" :key="item.value2" :label="item.label2" :value="item.value2">
            </el-option>
          </el-select>
        </el-col>

        <el-col :span="3">
          <div class="button">
            <el-button type="primary" style="float:right" round @click.native.prevent="confirm"></el-button>
          </div>
        </el-col>
      </el-row>
    </el-form>
    <el-table :data="tableData" style="height: 100%" :header-cell-style="{background:'#1a74ee',color:'#f9fafc'}">
      <el-table-column prop="tid" label="/" align="center"></el-table-column>
      <el-table-column prop="tname" label="/" align="center"></el-table-column>
      <el-table-column prop="postTask" label="" align="center"></el-table-column>
      <el-table-column prop="effectDegree" label="" align="center"></el-table-column>
      <el-table-column prop="predecessorTask" label="" align="center"></el-table-column>
      <el-table-column prop="dependencyDegree" label="" align="center"></el-table-column>
      <el-table-column fixed="right" label="" align="center">
        <template>
          <el-button @click="dialogTimeandCondition = true" type="text" icon="el-icon-upload"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="" :visible.sync="dialogTimeandCondition" width="40%" center>
      <div class="block">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="">
            <el-date-picker v-model="tasktime" type="date" placeholder="" format="yyyy  MM  dd "
              value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" v-model="price" clearable></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="condition">
        <el-table :data="tableData1" border style="width: 100%">
          <el-table-column prop="category" label="">
            <template slot-scope="scope">
              <template v-if="scope.row.edit">
                <el-cascader :options="options7" :show-all-levels="false" v-model="scope.row.label"></el-cascader>
              </template>
              <span v-else>{{ scope.row.label }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="professional" label="">
            <template slot-scope="scope">
              <template v-if="scope.row.edit">
                <el-select v-model="scope.row.professional.value" placeholder="">
                  <el-option v-for="item in scope.row.professional.options" :key="item.label" :label="item.label"
                    :value="item.label" :disabled="item.disabled">
                  </el-option>
                </el-select>
              </template>
              <span v-else>{{ scope.row.professional.value }}</span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <div class="btn-icons-group">
                <i class="edit el-icon-edit" @click="scope.row.edit=true;isAddRow=false"></i>
                <i class="delect el-icon-delete" @click="deleteRow(scope.$index, tableData1)">
                </i>
                <i class="save el-icon-upload2" @click="saveClick(scope.$index,scope.row.label)">
                </i>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="add-row" @click.prevent="addRow()"><span>+ </span></div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogTimeandCondition = false"> </el-button>
        <el-button type="primary" @click="dialogTimeandCondition = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tasktime: '',
      formInline: '',
      price: '',
      isAddRow: true,
      value7: '',
      loading: true, // loading
      dialogTimeandCondition: false,
      projectid: [],
      taskcode: [],
      taskname: [],

      form: {
        projectId: '',
        tid: '',
        tname: '',
        taskClass: 'T',
        effectDegree: '',
        dependencyDegree: ''
      },
      formLabelWidth: '72px',
      // select
      options: [{
        value: 'T',
        label: ''
      }, {
        value: 'G',
        label: ''
      }],
      options1: [{
        value1: '1',
        label1: ''
      }, {
        value1: '2',
        label1: '<=2'
      }, {
        value1: '3',
        label1: '<=5'
      }, {
        value1: '4',
        label1: '<=8'
      }, {
        value1: '5',
        label1: '<=12'
      }, {
        value1: '6',
        label1: '>12'
      }],
      options2: [{
        value2: '1',
        label2: ''
      }, {
        value2: '2',
        label2: '<=2'
      }, {
        value2: '3',
        label2: '<=5'
      }, {
        value2: '4',
        label2: '<=8'
      }, {
        value2: '5',
        label2: '<=12'
      }, {
        value2: '6',
        label2: '>12'
      }],
      tableData: [{
        tid: '',
        tname: '',
        postTask: '',
        effectDegree: '',
        predecessorTask: '',
        dependencyDegree: ''
      }],
      tableData1: [{
        id: '1', // id
        category: {
          value: '',
          options: [{
            label: ''
          },
          {
            label: ''
          }
          ]
        },
        professional: {
          value: 'Java',
          options: [{
            label: 'Java'
          },
          {
            label: 'Js'
          }
          ]
        },
        usageTime: '',
        mastery: {
          value: '',
          options: [{
            label: ''
          },
          {
            label: ''
          },
          {
            label: ''
          }
          ]
        },
        recentUsage: '2018-09-07',
        edit: false
      }],
      list: {
        id: null, // id
        category: {
          value: null,
          options: [{
            label: 'Java'
          },
          {
            label: 'Js'
          }
          ]
        },
        professional: {
          value: null,
          options: [{
            label: 'Java'
          },
          {
            label: 'Js'
          }
          ]
        },
        usageTime: null,
        mastery: {
          value: '',
          options: [{
            label: ''
          },
          {
            label: ''
          },
          {
            label: ''
          }
          ]
        },
        recentUsage: null,
        edit: true
      },
      options7: []
    }
  },
  methods: {
    change (value) {
      console.log(value)
    },
    taskTypeChage (val) {
      console.log(val)
    },
    handleClick (row) {
      console.log(row)
    },
    // 
    querySearch (queryString, cb) {
      var taskBlurry = this.projectid
      // console.log(taskBlurry)
      var results = queryString ? taskBlurry.filter(this.createFilter(queryString)) : taskBlurry
      //  callback 
      cb(results)
    },
    createFilter (queryString) {
      return (taskBlurry) => {
        return (taskBlurry.value.toLowerCase().indexOf(queryString.toLowerCase()) > -1)
      }
    },
    handleSelect (item) {
      console.log(item)
    },
    // 
    addRow () {
      if (this.isAddRow) {
        this.tableData1.push(Object.assign({}, this.list))
        this.isAddRow = false
      } else {
        this.$notify({
          title: '',
          message: '',
          type: 'warning',
          offset: 100
        })
        return false
      }
    },
    // 
    saveClick (index, row) {
      if (row.edit === false) {
        // 
        this.$message('')
        return false
      }
      // this.loading = true
      row.edit = false
      this.isAddRow = true
      let formData = new FormData()
      Object.keys(this.list).forEach(function (key) {
        formData.append(key, row[key]) // formData
      })
      this.saveSubmit(index, formData)
    },
    // 
    deleteRow (index, rows) {
      if (rows[index].edit === true) { // isAddRowtrue,
        this.isAddRow = true
      }
      if (!rows[index].id) {
        rows.splice(index, 1) // id
        return false
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // this.loading = true
        var currData = rows[index]
        this.$api.jsnl.delTechnologicalCapability(currData).then(res => {
          this.$message({
            type: 'success',
            message: '!'
          })
          // this.loading = false
        }).catch(res => {
          this.$message({
            type: 'error',
            message: '!'
          })
          // this.loading = false
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  mounted () {
    this.projectId = this.$route.query.date
    // 
    this.$api.TaskCollection.getProject().then(res => {
      let result = res.data
      console.log(result.data)
      this.projectid = result.data
    })
  }
}

</script>
<style lang="scss" scoped>
  .el-row {
    margin-bottom: 30px;
    margin-top: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .el-form {
    margin-bottom: 20px;
  }

  .el-col {
    border-radius: 4px;
  }

  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple-light {
    background: #e5e9f2;
  }

  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }

  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }

  .input1 {
    width: 100%;

    .el-input__inner {
      height: 40px;
      width: 100%;
      border-radius: 8px;
      border: 1px solid #DCDFE6;
    }
  }

  .select1 {
    .el-input__inner {
      height: 40px;
      border-radius: 8px;
      border: 1px solid #DCDFE6;
    }
  }

  .button {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .el-input__inner {
    border-radius: 5px;
    border: 1px solid #DCDFE6;
  }

  .input {
    .el-input__inner {
      border-radius: 5px;
      border: 1px solid #DCDFE6;
    }
  }

</style>



