<template>
  <box-wrap>
    <template slot="boxHeaderTitle"></template>
    <template slot="boxBodyInner">
      <el-table :data="tableData" border style="width: 100%" v-loading="loading">
        <el-table-column prop="skillCategory" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.skillCategory" placeholder="">
                <el-option v-for="item in skillCategory" :key="item.dictCode" :label="item.dictName" :value="item.dictCode"
                  :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatCategory(scope.row.skillCategory) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="skill" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.skill" placeholder="">
                <el-option v-for="item in skill" :key="item.dictCode" :label="item.dictName" :value="item.dictCode"
                  :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatProfession(scope.row.skill) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="usageTime" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-input class="ipt" v-model="scope.row.usageTime"></el-input>
            </template>
            <span v-else>{{ scope.row.usageTime }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="masteryLevel" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.masteryLevel" placeholder="">
                <el-option v-for="item in masteryLevel" :key="item.dictCode" :label="item.dictName" :value="item.dictCode"
                  :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatMastery(scope.row.masteryLevel) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="recentUsage" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-date-picker v-model="scope.row.recentUsage" type="date" placeholder="" value-format="yyyy-MM-dd">
              </el-date-picker>
            </template>
            <span v-else>{{ scope.row.recentUsage }}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="btn-icons-group">
              <i class="edit el-icon-edit" @click="scope.row.edit=true;isAddRow=false"></i>
              <i class="delect el-icon-delete" @click="deleteRow(scope.$index, tableData)">
              </i>
              <i class="save el-icon-upload2" @click="saveClick(scope.$index,scope.row)">
              </i>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="add-row" @click.prevent="addRow()"><span>+ </span></div>
    </template>
  </box-wrap>
</template>
<script>
// box**
import BoxWrap from '@/components/box.vue'
export default {
  components: {
    BoxWrap
  },
  data () {
    return {
      isAddRow: true, // 
      loading: false, // loading
      // 
      skillCategory: [
        {
          dictCode: '1',
          dictName: ''
        },
        {
          dictCode: '2',
          dictName: ''
        }
      ],
      // 
      skill: [
        {
          dictCode: '1',
          dictName: 'Js'
        },
        {
          dictCode: '2',
          dictName: 'Java'
        },
        {
          dictCode: '3',
          dictName: 'vue'
        }
      ],
      // 
      masteryLevel: [
        {
          dictCode: '1',
          dictName: ''
        },
        {
          dictCode: '2',
          dictName: ''
        }
      ],
      list: {
        id: null, // id
        skillCategory: '',
        skill: '',
        usageTime: '',
        masteryLevel: '',
        recentUsage: '',
        edit: true
      },
      tableData: [{
        id: '1', // id
        skillCategory: '1',
        skill: '2',
        usageTime: '',
        masteryLevel: '1',
        recentUsage: '2018-09-07',
        edit: false
      }]
    }
  },
  created () {
    let dictionaryObj = {
      dict_code: [ 'skillCategory', 'skill', 'masteryLevel' ]
    }
    this.$api.dictionary.getDictionaries(dictionaryObj).then(res => {
      let result = res.data
      let dictionary = {}
      result.data.forEach(item => {
        Object.assign(dictionary, item)
      })
      this.skillCategory = dictionary.skillCategory
      this.skill = dictionary.skill
      this.masteryLevel = dictionary.masteryLevel
    })

    // tokentokenhttp header,api
    this.$api.jsnl.queryTechnologicalCapability().then(res => {
      let result = res.data
      if (result.status === '1') {
        if (!result.data) {
          this.tableData = []
        } else {
          this.tableData = result.data
        }
      } else {
        this.$message('')
      }
    })
  },
  methods: {
    formatCategory (value) {
      let currObj = this.skillCategory.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    formatProfession (value) {
      let currObj = this.skill.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    formatMastery (value) {
      let currObj = this.masteryLevel.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    // 
    addRow () {
      if (this.isAddRow) {
        this.tableData.push(Object.assign({}, this.list))
        this.isAddRow = false
      } else {
        this.$notify({
          title: '',
          message: '',
          type: 'warning',
          offset: 100
        })
        return false
      }
    },
    // 
    saveClick (index, row) {
      if (row.edit === false) {
        // 
        this.$message('')
        return false
      }
      this.loading = true
      let formData = new FormData()
      Object.keys(this.list).forEach(function (key) {
        formData.append(key, row[key]) // formData
      })
      this.saveSubmit(index, formData)
    },
    // 
    saveSubmit (index, formData) {
      let row = this.tableData[index]
      this.$api.jsnl.saveTechnologicalCapability(formData).then(res => {
        this.loading = false
        let result = res.data// idID
        if (result.status === '1') {
          this.$message({
            type: 'success',
            message: ''
          })
          row.edit = false
          this.isAddRow = true
          row = result.data
          this.tableData.splice(index, 1, row)
        } else {
          this.$message({
            type: 'error',
            message: ''
          })
        }
      })
    },
    // 
    deleteRow (index, rows) {
      if (rows[index].edit === true) { // isAddRowtrue,
        this.isAddRow = true
      }
      if (!rows[index].id) {
        rows.splice(index, 1) // id
        return false
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.loading = true
        var currData = rows[index]
        this.$api.jsnl.delTechnologicalCapability(currData).then(res => {
          let result = res.data
          if (result.status === '1') {
            this.$message({
              type: 'success',
              message: '!'
            })
            rows.splice(index, 1)
          } else {
            this.$message({
              type: 'error',
              message: '!'
            })
          }

          this.loading = false
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  activated () {}
}

</script>
<style scoped lang="scss">
  /* css */

</style>



