
import axios from '@/utils/http' // httpaxios
// import Qs from 'qs'
const TaskGroup = {
  // 
  getProject (params) {
    return axios({
      method: 'post',
      url: '/taskPublish/getProject'
      // data: params
    })
  },
  // Tree
  initTaskGroupTree (params) {
    return axios({
      method: 'post',
      url: '/taskSet/initTaskGroupTree',
      data: params
    })
  },
  // 
  getTaskGroupTree (params) {
    return axios({
      method: 'post',
      url: '/taskSet/getTaskGroupTree',
      data: params
    })
  },
  // 
  getTaskList (params) {
    return axios({
      method: 'post',
      url: '/taskSet/getTaskList',
      data: params
    })
  },
  // 
  saveTaskGroupInfo (params) {
    return axios({
      method: 'post',
      url: '/taskSet/saveTaskGroupInfo',
      data: params
    })
  },
  // 
  deleteTaskGroupInfo (params) {
    return axios({
      method: 'post',
      url: '/taskSet/deleteTaskGroupInfo',
      data: params
    })
  },
  // 
  saveTaskGroup (params) {
    return axios({
      method: 'post',
      url: '/taskSet/saveTaskGroup',
      data: params
    })
  }

}
export default TaskGroup



