
import axios from '@/utils/http' // httpaxios
const trainingExperience = {
  // 
  saveresume (params) {
    return axios({
      method: 'post',
      url: '/resume/saveresume',
      data: params
    })
  },
  // 
  queryResumeby (params) {
    return axios({
      method: 'post',
      url: '/resume/queryResumeby'
      // data: params
    })
  },
  // 
  delresume (params) {
    return axios({
      method: 'post',
      url: '/resume/delresume',
      data: params
    })
  },
  // 
  saveEnclosure (params) {
    return axios({
      method: 'post',
      url: '/enclosure/saveEnclosure',
      data: params
    })
  },
  delEnclosureSingle (params) {
    return axios({
      method: 'post',
      url: '/enclosure/delEnclosureSingle',
      data: params
    })
  }
}
export default trainingExperience



