<template>
  <el-menu :router="true"
  :default-active="defaultActive">
    <el-menu-item index="2-1"></el-menu-item>
      <el-menu-item index="2-2"></el-menu-item>
      <el-menu-item index="2-3"></el-menu-item>
      <el-menu-item index="2-4"></el-menu-item>
      <el-menu-item index="2-5"></el-menu-item>
      <el-menu-item index="2-6"></el-menu-item>
  </el-menu>
</template>
<script>
export default {
  data () {
    return {}
  },
  computed: {
    defaultActive () {
      return this.$route.path.split('/')[1]
    }
  }

}

</script>
<style lang="scss">

</style>



