<template>
 <box-wrap>
    <template slot="boxHeaderTitle"></template>
    <template slot="boxBodyInner">
      <el-table :data="tableData" border style="width: 100%" v-loading="loading">
        <el-table-column prop="projectName" label="">
        </el-table-column>
        <el-table-column prop="taskId" label="">
        </el-table-column>
        <el-table-column prop="taskName" label="">
        </el-table-column>
        <el-table-column prop="postTask" label="">
        </el-table-column>
        <el-table-column prop="effectDegree" label="">
        </el-table-column>
        <el-table-column prop="predecessorTask" label="">
        </el-table-column>
        <el-table-column prop="dependencyDegree" label="">
        </el-table-column>
         <el-table-column prop="recentUsage" label="">
        </el-table-column>
         <el-table-column prop="recentUsage" label="">
        </el-table-column>
         <el-table-column prop="recentUsage" label="">
        </el-table-column>
         <el-table-column prop="recentUsage" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="btn-icons-group">
              <i class="edit el-icon-edit" @click="scope.row.edit=true;isAddRow=false"></i>
              <i class="delect el-icon-delete" @click="deleteRow(scope.$index, tableData)">
              </i>
              <i class="save el-icon-upload2" @click="saveClick(scope.$index,scope.row)">
              </i>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </box-wrap>
</template>
<script>
// box**
import BoxWrap from '@/components/box.vue'
export default {
  components: {
    BoxWrap
  },
  data () {
    return {
      tableData: []
    }
  },
  methods: {
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>

</style>



