
<template>
<div class="box-table">
<el-form :inline="true" :model="questInfo" >
  <el-form-item label="">
    <el-input v-model="questInfo.quTitle" placeholder=""></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="questInfo.quDirType" placeholder="">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit"></el-button>
  </el-form-item>
</el-form>
<el-row>
  <el-col :span="2"><div class="grid-content bg-purple"><el-button type="primary" @click="questAdd = true" icon="el-icon-edit"></el-button></div></el-col>
  <el-col :span="2"><div class="grid-content bg-purple"><el-button type="primary" @click="questAdd = true" icon="el-icon-edit"></el-button></div></el-col>
  <el-col :span="3"><div class="grid-content bg-purple"><el-button type="primary" @click="questOptionsAdd = true" icon="el-icon-document" ></el-button></div></el-col>
  <el-col :span="1"><div class="grid-content bg-purple"><el-button type="primary" @click="delData" icon="el-icon-delete" ></el-button></div></el-col>
</el-row>
<el-row>
   <el-table :data="tableData" border @selection-change="handleSelectionChange_question" style="width: 100%">
    <el-table-column  type="selection"  width="55"  align="center"></el-table-column>
    <el-table-column prop="quTitle" label="" width="180"></el-table-column>
    <el-table-column prop="quDirType" label="" width="180"></el-table-column>
    <el-table-column prop="quAnswerType" label="" width="180"></el-table-column>
    <el-table-column prop="quOpDescFlag" label="" width="180"></el-table-column>
    <el-table-column prop="createUserName" label="" width="160"></el-table-column>
    <el-table-column prop="createDate" label="" width="170"></el-table-column>
    <el-table-column  label="" width="180"></el-table-column>
  </el-table>
</el-row>

<el-row >
  <div class="block">
    <el-pagination background layout="total,prev, pager, next,jumper" :total="1000"></el-pagination>
  </div>
</el-row>
<el-dialog title="" :visible.sync="questAdd"  center>
<el-form  label-width="120px"  :rules="addQuestionRules" ref="questInfo"  :model="questInfo" >
<el-form-item label="" prop="quTitle" >
  <el-input v-model="questInfo.quTitle"  placeholder="" size="medium"  ></el-input>
</el-form-item>
<el-form-item label="" prop="quDirType" >
    <el-select v-model="questInfo.quDirType" placeholder="" size="medium">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
    </el-select>
</el-form-item>
<el-form-item label="" prop="quAnswerType">
    <el-select v-model="questInfo.quAnswerType" placeholder="" size="medium">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
      <el-option label="" value="3"></el-option>
      <el-option label="" value="4"></el-option>
    </el-select>
</el-form-item>
<el-form-item label="" prop="quOpDescFlag">
  <el-switch v-model="questInfo.quOpDescFlag" @change="switchToggle"  active-color="#13ce66" inactive-color="#ff4949"> </el-switch>
</el-form-item>
<el-form-item label="" v-show="questInfo.quOpDescFlag" prop="quOpDesc">
  <el-input type="textarea"  :rows="5" v-show="questInfo.quOpDescFlag" placeholder="" v-model="questInfo.quOpDesc"></el-input>
</el-form-item>
<el-form-item>
  <el-button type="primary" @click.native.prevent="saveQuestion('questInfo')"  round></el-button>
</el-form-item>
</el-form>
</el-dialog>

<el-dialog title="" :visible.sync="questOptionsAdd"  ccustom-class="dialog-width" fullscreen center>
  <el-form :inline="true" :model="questOptions" class="demo-form-inline">
    <el-form-item label="">
      <el-input v-model="questOptions.quTitle" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="questOptions.quOpType" placeholder="" size="medium">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="2"></el-option>
        <el-option label="" value="3"></el-option>
        <el-option label="" value="4"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit1"></el-button>
    </el-form-item>
  </el-form>
  <el-row>
    <el-col :span="2"><div class="grid-content bg-purple"><el-button type="primary" @click="optionAdd = true" icon="el-icon-edit"></el-button></div></el-col>
    <el-col :span="2"><div class="grid-content bg-purple"><el-button type="primary" @click="optionAdd = true" icon="el-icon-edit"></el-button></div></el-col>
    <el-col :span="1"><div class="grid-content bg-purple"><el-button type="primary" @click="optionDel" icon="el-icon-delete" ></el-button></div></el-col>
  </el-row>
  <el-row>
   <el-table :data="tableDataOpt" border style="width: 100%">
    <el-table-column  type="selection"  width="55"  align="center"></el-table-column>
    <el-table-column prop="quTitle" label="" width="230"></el-table-column>
    <el-table-column prop="quOpType" label="" width="230"></el-table-column>
    <el-table-column prop="quOpSign" label="" width="230"></el-table-column>
    <el-table-column prop="quOpScore" label="" width="230"></el-table-column>
    <el-table-column prop="quOpOrder" label="" width="230"></el-table-column>
    <el-table-column prop="quOpDescFlag" label="" width="230"></el-table-column>
    <el-table-column prop="quOpdesc" label="" width="200"></el-table-column>
  </el-table>
</el-row>
<el-dialog title="" :visible.sync="optionAdd"  append-to-body center>
  <el-form  label-width="80px"  :model="qots" >
    <el-form-item label="">
      <el-input v-model="qots.quTitle"  placeholder=""></el-input>
    </el-form-item>
    <!--<el-form-item label="">
      <el-input v-model="qots.quOpType"  placeholder=""></el-input>
    </el-form-item>-->
    <el-form-item label="">
      <el-input v-model="qots.quOpSign"  placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="qots.quOpScore"  :min="0" :max="100" label=""></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="qots.quOpOrder"  :min="1" :max="10" label=""></el-input-number>
    </el-form-item>
    <el-form-item label="">
  <el-switch v-model="qots.quOpDescFlag" active-color="#13ce66" inactive-color="#ff4949"> </el-switch>
</el-form-item>
<el-form-item label="" v-show="qots.quOpDescFlag">
  <el-input type="textarea"  :rows="5" v-show="qots.quOpDescFlag" placeholder="" v-model="qots.quOpDesc"></el-input>
</el-form-item>
<el-form-item>
  <el-button type="primary" @click="optionAdd = false"  round></el-button>
</el-form-item>
  </el-form>
</el-dialog>
</el-dialog>

</div>
</template>

<script>
export default {
  data () {
    return {
      multipleSelection: [],
      questInfo: {
        quTitle: '',
        quDirType: '',
        quAnswerType: '',
        quOpDescFlag: 0,
        quOpDesc: ''
      },
      addQuestionRules: {
        quTitle: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }
        ],
        quDirType: [
          {required: true, message: '', trigger: 'change'}
        ],
        quAnswerType: [
          {required: true, message: '', trigger: 'change'}
        ]/* ,
        quOpDesc: [
          {required: this.questInfo.quOpDescFlag, message: '', trigger: 'blur'}
        ] */
      },
      questOptions: {
        quTitle: '',
        quOpType: ''
      },
      qots: {
        quTitle: '',
        quOpType: '',
        quOpSign: '',
        quOpScore: 0,
        quOpOrder: 1,
        quOpDescFlag: 0,
        quOpDesc: ''
      },
      questAdd: false,
      questOptionsAdd: false,
      optionAdd: false,
      value1: false,
      tableData: [
        {
          // 
          quTitle: '',
          quDirType: '',
          quAnswerType: '',
          quOpDescFlag: '',
          quOpDesc: '111111111',
          createUserName: 'admin',
          createDate: '2019-4-16'
        },
        {
          // 
          quTitle: '',
          quDirType: '',
          quAnswerType: '',
          quOpDescFlag: '',
          quOpDesc: '111111111',
          createUserName: 'admin',
          createDate: '2019-4-16'
        },
        {
          // 
          quTitle: '',
          quDirType: '',
          quAnswerType: '',
          quOpDescFlag: '',
          quOpDesc: '111111111',
          createUserName: 'admin',
          createDate: '2019-4-16'
        }
      ],
      tableDataOpt: [
        {
          // 
          quTitle: '',
          quOpType: '',
          quOpSign: 'A',
          quOpScore: '1',
          quOpOrder: '0',
          quOpDescFlag: '',
          quOpdesc: '123'
        },
        {
          // 
          quTitle: '',
          quOpType: '',
          quOpSign: 'A',
          quOpScore: '1',
          quOpOrder: '0',
          quOpDescFlag: '',
          quOpdesc: '123'
        },
        {
          // 
          quTitle: '',
          quOpType: '',
          quOpSign: 'A',
          quOpScore: '1',
          quOpOrder: '0',
          quOpDescFlag: '',
          quOpdesc: '123'
        }
      ]

    }
  },
  mounted () {
    this.$api.questionBanks.getQuestionBankList().then(res => {
      let result = res.data
      this.tableData = result.data
    })
  },
  methods: {
    onSubmit () {
      console.log('submit')
    },
    onSubmit1 () {
      console.log('submit1')
    },
    saveQuestion (valForm) {
      console.log(this.questInfo)
      this.$refs[valForm].validate((valid) => {
        if (valid) {
          this.$api.questionBanks.saveQuestionBank(this.questInfo).then(res => {
            let result = res.data
            console.log(result)
            if (result.state === '1') {
              this.$message({
                type: 'success',
                message: '!'
              })
            } else {
              this.$message.error('')
            }
            this.questAdd = false
          })
        } else {
          return false
        }
      })
    },
    questDel () {
      console.log('edit')
    },
    optionDel () {

    },
    delData () {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      } else {
        this.$message({
          type: 'warning',
          message: ''
        })
      }
    },
    switchToggle (val) {
      let quOpDescRule = [
        {required: val, message: '', trigger: 'blur'},
        { min: 1, max: 10, message: ' 1  200 ', trigger: 'blur' }
      ]
      if (val) {
        this.addQuestionRules.quOpDesc = quOpDescRule
      } else {
        this.addQuestionRules.quOpDesc = []
      }
    },
    // --
    handleSelectionChange_question (val) {
      this.multipleSelection = val
      console.log(this.multipleSelection)
    }
  }

}
</script>
<style lang="scss" scoped>
/* .input10{
 .el-input__inner {
      height: 40px;
      width: 200px;
      border-top: 1px;
      border-left: 1px;
      border-right: 1px;
      border-bottom: 1px;
    }
} */
.dialog-width{
min-width: 600rem !important;
}
.el-pagination{
  margin-top:20px;
  text-align: right;
}
.el-input{
  border-top:1px solid #DCDFE6;
  border-left:1px solid #DCDFE6;
  border-right:1px solid #DCDFE6;
}
</style>



