import axios from '@/utils/http' // httpaxios
// import Qs from 'qs'
const TaskCreate = {
  // 1
  getProject () {
    return axios({
      method: 'post',
      url: '/taskCount/getProject'
      // data: params
    })
  },
  //  
  claim (params) {
    return axios({
      method: 'post',
      url: '/taskClaim/claim',
      data: params
    })
  },
  // 
  getCountTaskList (params) {
    return axios({
      method: 'post',
      url: '/taskCount/getCountTaskList',
      data: params
    })
  }

}
export default TaskCreate



