<template>
<div class="box-table">
<el-form :inline="true" :model="quer" class="demo-form-inline">
  <el-form-item label="">
    <el-input v-model="quer.quTitle" placeholder=""></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="quer.quType" placeholder="">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
      <el-option label="" value="3"></el-option>
      <el-option label="" value="4"></el-option>
      <el-option label="" value="5"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit"></el-button>
  </el-form-item>
</el-form>

<el-row>
  <el-col :span="2"><div class="grid-content bg-purple"><el-button type="primary" @click="questAdd = true" icon="el-icon-edit"></el-button></div></el-col>
  <el-col :span="2"><div class="grid-content bg-purple"><el-button type="primary" @click="questAdd = true" icon="el-icon-edit"></el-button></div></el-col>
  <el-col :span="3"><div class="grid-content bg-purple"><el-button type="primary" @click="questSel = true" icon="el-icon-document" ></el-button></div></el-col>
  <el-col :span="1"><div class="grid-content bg-purple"><el-button type="primary" @click="delData" icon="el-icon-delete" ></el-button></div></el-col>
</el-row>
<el-row>
   <el-table :data="tableData" border style="width: 100%">
    <el-table-column  type="selection"  width="55"  align="center"></el-table-column>
    <el-table-column prop="quTitle" label="" width="200"></el-table-column>
    <el-table-column prop="quType" label="" width="200"></el-table-column>
    <el-table-column prop="quDescFlag" label="" width="200"></el-table-column>
    <el-table-column prop="quDesc" label="" width="230"></el-table-column>
    <el-table-column prop="createUserName" label="" width="200"></el-table-column>
    <el-table-column prop="createDate" label="" width="200"></el-table-column>
  </el-table>
</el-row>
<el-row >
  <div class="block">
    <el-pagination background layout="total,prev, pager, next,jumper" :total="1000"></el-pagination>
  </div>
</el-row>
<!--  start-->
<el-dialog title="" :visible.sync="questAdd"  center>
<el-form  label-width="80px"  :model="querInfo" >
<el-form-item label="" prop="quTitle" >
  <el-input v-model="querInfo.quTitle"  placeholder="" size="medium"  ></el-input>
</el-form-item>
<el-form-item label="">
    <el-select v-model="querInfo.quType" placeholder="" size="medium">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
      <el-option label="" value="3"></el-option>
      <el-option label="" value="4"></el-option>
      <el-option label="" value="5"></el-option>
    </el-select>
</el-form-item>
<el-form-item label="">
  <el-switch v-model="querInfo.quDescFlag" active-color="#13ce66" inactive-color="#ff4949"> </el-switch>
</el-form-item>
<el-form-item label="" v-show="querInfo.quDescFlag">
  <el-input type="textarea"  :rows="5" v-show="querInfo.quDescFlag" placeholder="" v-model="querInfo.quDesc"></el-input>
</el-form-item>
<el-form-item>
  <el-button type="primary" @click="questAdd = false"  round></el-button>
</el-form-item>
</el-form>
</el-dialog>
<!-- end-->

<!--  start-->
<el-dialog title="" :visible.sync="questSel"  center>
  <el-transfer filterable :filter-method="filterMethod" filter-placeholder=""
  :titles="['', '']"
  :left-default-checked="[2, 3]"
  :right-default-checked="[1]"
  v-model="value4"
  :data="dataOpts">
  </el-transfer>
</el-dialog>
<!--  end-->
</div>
</template>
<script>
export default {
  data () {
    return {
      quer: {
        quTitle: '',
        quType: ''
      },
      querInfo: {
        quTitle: '',
        quType: '',
        quDescFlag: 0,
        quDesc: ''
      },
      dataOpts: [
        {
          label: '',
          key: 0
        },
        {
          label: '',
          key: 1
        },
        {
          label: '',
          key: 2
        },
        {
          label: '',
          key: 3
        },
        {
          label: '',
          key: 4
        },
        {
          label: '',
          key: 5
        },
        {
          label: '',
          key: 6
        }
      ],
      questAdd: false,
      questSel: false,
      value4: [1, 5],
      tableData: [
        {
          // 
          quTitle: '1',
          quType: '',
          quDescFlag: '',
          quDesc: '111111111',
          createUserName: 'admin',
          createDate: '2019-4-16'
        },
        {
          // 
          quTitle: '1',
          quType: '',
          quDescFlag: '',
          quDesc: '111111111',
          createUserName: 'admin',
          createDate: '2019-4-16'
        },
        {
          // 
          quTitle: '1',
          quType: '',
          quDescFlag: '',
          quDesc: '111111111',
          createUserName: 'admin',
          createDate: '2019-4-16'
        }
      ]
    }
  },
  methods: {
    onSubmit () {
      console.log('submit')
    },
    delData () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    filterMethod (query, item) {
      return item.label.indexOf(query) > -1
    }
  }
}
</script>
<style lang="scss" scoped>
.el-pagination{
  margin-top:20px;
  text-align: right;
}
.el-transfer{
  height: 300px;
}
</style>



