<template>
  <div class="base-info">
    <!--  -->

    <el-row>
      <el-col :span="4">
        <box-wrap>
          <template slot="boxHeaderTitle">
            
          </template>
        </box-wrap>
      </el-col>
      <el-col :span="20">
        <div class="top-btns">
          <el-button class="add-row" @click.prevent="addRow()"><span></span></el-button>
          <el-button class="add-row"><span></span></el-button>
        </div>
      </el-col>
    </el-row>
    <el-form>
      <el-table :data="tableData" :border="true" style="width: 100%">
        <el-table-column prop="date" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-date-picker v-model="scope.row.date" type="daterange" range-separator="" start-placeholder=""
                end-placeholder="" value-format="yyyy-MM-dd">
              </el-date-picker>
            </template>
            <span v-else>{{getDateStr1(scope.row)}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="industry" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.industry">
                <el-option v-for="item in industry" :key="item.dictCode" :label="item.dictName" :value="item.dictCode"
                  :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatIndustry(scope.row.industry) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="projectName" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-input class="ipt" size="small" v-model="scope.row.trainingmode"></el-input>
            </template>
            <template v-else>
              <span>{{ scope.row.projectName }}</span>
              <el-popover trigger="hover" placement="top">
                <p>{{ scope.row.projectName }}</p>
                <p>{{ scope.row.content }}</p>
                <i slot="reference" class="project-notice-icon"></i>
              </el-popover>
            </template>
          </template>
        </el-table-column>
        <el-table-column prop="projectScale" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.projectScale">
                <el-option v-for="item in projectScale" :key="item.dictCode" :label="item.dictName"
                  :value="item.dictCode" :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatProjectScale(scope.row.projectScale) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="projectRole" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.projectRole">
                <el-option v-for="item in projectRole" :key="item.dictCode" :label="item.dictName"
                  :value="item.dictCode" :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatProjectRole(scope.row.projectRole) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="duties" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-input class="ipt" size="small" v-model="scope.row.duties"></el-input>
            </template>
            <span v-else>{{scope.row.duties}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="involvingBusiness" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.involvingBusiness">
                <el-option v-for="item in involvingBusiness" :key="item.dictCode" :label="item.dictName"
                  :value="item.dictCode" :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatInvolvingBusiness(scope.row.involvingBusiness) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="skill" label="">
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-model="scope.row.skill">
                <el-option v-for="item in skill" :key="item.dictCode" :label="item.dictName" :value="item.dictCode"
                  :disabled="item.disabled">
                </el-option>
              </el-select>
            </template>
            <span v-else>{{ formatSkill(scope.row.skill) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="btn-icons-group">
              <i class="edit el-icon-edit" @click="scope.row.edit=true;isAddRow=false"></i>
              <i class="delect el-icon-delete" @click="deleteRow(scope.$index, tableData)">
              </i>
              <i class="save el-icon-upload2" @click="saveClick(scope.$index,scope.row)">
              </i>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </el-form>

  </div>
</template>
<script>
import BoxWrap from '@/components/box.vue'
export default {
  components: {
    BoxWrap
  },
  data () {
    return {
      industry: [{
        dictCode: '1',
        dictName: ''
      },
      {
        dictCode: '2',
        dictName: ''
      }
      ],
      projectScale: [{
        dictCode: '1',
        dictName: ''
      },
      {
        dictCode: '2',
        dictName: ''
      },
      {
        dictCode: '3',
        dictName: ''
      }
      ],
      projectRole: [{
        dictCode: '1',
        dictName: ''
      },
      {
        dictCode: '2',
        dictName: ''
      }
      ],
      involvingBusiness: [{
        dictCode: '1',
        dictName: ''
      },
      {
        dictCode: '2',
        dictName: ''
      }
      ],
      skill: [{
        dictCode: '1',
        dictName: ''
      },
      {
        dictCode: '2',
        dictName: ''
      }
      ],
      isAddRow: true, // 
      loading: false, // loading
      list: {
        id: null, // id
        date: '',
        industry: '',
        projectName: '',
        projectScale: '',
        projectRole: '',
        duties: '',
        involvingBusiness: '',
        sill: '',
        edit: true
      },
      tableData: [{
        id: '1', // idid
        date: ['2019-04-05', '2019-09-08'],
        industry: 1,
        projectName: '',
        projectScale: '',
        projectRole: '',
        duties: '',
        involvingBusiness: '',
        skill: '',
        content: '',
        edit: false
      },
      {
        id: '2',
        date: '',
        industry: 2,
        projectName: '',
        projectSize: '1',
        projectRole: '2',
        duties: '',
        business: '1',
        skill: '1',
        content: '',
        edit: false
      }
      ]
    }
  },
  created () {
    let dictionaryObj = {
      dict_code: ['industry', 'projectScale', 'projectRole', 'involvingBusiness', 'skill']
    }
    this.$api.dictionary.getDictionaries(dictionaryObj).then(res => {
      let result = res.data
      let dictionary = {}
      result.data.forEach(item => {
        Object.assign(dictionary, item)
      })
      this.industry = dictionary.industry
      this.projectScale = dictionary.projectScale
      this.projectRole = dictionary.projectRole
      this.involvingBusiness = dictionary.involvingBusiness
      this.skill = dictionary.skill
    })

    // tokentokenhttp header,api
    this.$api.resoftProject.queryResoftProject().then(res => {
      let result = res.data
      if (result.status === '1') {
        this.tableData = result.data || []
      } else {
        this.$message('')
      }
    })
  },
  methods: {
    formatIndustry (value) {
      let currObj = this.industry.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    formatProjectScale (value) {
      let currObj = this.projectScale.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    formatProjectRole (value) {
      let currObj = this.projectRole.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    formatInvolvingBusiness (value) {
      let currObj = this.involvingBusiness.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    formatSkill (value) {
      let currObj = this.skill.filter(obj => {
        return obj.dictCode === value
      })
      return currObj.length > 0 ? currObj[0].dictName : ''
    },
    // 
    getDateStr1 (row) {
      if (!row.date) {
        return ''
      }
      let dateStr = `${row.date[0]}   ${row.date[1]}`
      return dateStr
    },
    // 
    addRow () {
      if (this.isAddRow) {
        this.tableData.push(Object.assign({}, this.list))
        this.isAddRow = false
      } else {
        this.$notify({
          title: '',
          message: '',
          type: 'warning',
          offset: 100
        })
        return false
      }
    },
    // 
    saveClick (index, row) {
      if (row.edit === false) {
        // 
        this.$message('')
        return false
      }
      this.loading = true
      let formData = new FormData()
      Object.keys(this.list).forEach(function (key) {
        formData.append(key, row[key]) // formData
      })
      this.saveSubmit(index, formData)
    },
    // 
    saveSubmit (index, formData) {
      let row = this.tableData[index]
      this.$api.resoftProject.saveResoftProject(formData).then(res => {
        this.loading = false
        let result = res.data // idID
        if (result.status === '1') {
          row.edit = false
          this.isAddRow = true
          row = result.data
          this.tableData.splice(index, 1, row)
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.$message({
            type: 'success',
            message: ''
          })
        }
      })
    },
    // 
    deleteRow (index, rows) {
      if (rows[index].edit === true) { // isAddRowtrue,
        this.isAddRow = true
      }
      if (!rows[index].id) {
        rows.splice(index, 1) // id
        return false
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.loading = true
        var currData = rows[index]
        this.$api.resoftProject.delresume(currData).then(res => {
          let result = res.data
          if (result.status === '1') {
            this.$message({
              type: 'success',
              message: '!'
            })
            rows.splice(index, 1)
          } else {
            this.$message({
              type: 'error',
              message: '!'
            })
          }
          this.loading = false
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}

</script>
<style lang="scss" scoped>
  .top-btns {
    float: right;
  }

  .project-notice-icon {
    width: 5px;
    height: 16px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px;
    background: url('~@assets/imgs/project-notice-icon.png') center no-repeat;
  }

</style>



