<template>
  <el-menu :router="true" :default-active="defaultActive">
    <el-menu-item index="1-1"></el-menu-item>
    <el-menu-item index="1-2"></el-menu-item>
    <el-menu-item index="1-3"></el-menu-item>
    <el-menu-item index="1-4"></el-menu-item>
    <el-menu-item index="1-5"></el-menu-item>
    <el-menu-item index="1-6"></el-menu-item>
  </el-menu>
</template>
<script>
export default {
  data () {
    return {}
  },
  computed: {
    defaultActive () {
      return this.$route.path.split('/')[1]
    }
  }

}

</script>
<style lang="scss">

</style>



