import axios from '@/utils/http' // httpaxios
// import Qs from 'qs'
const taskIssue = {
  // 1
  getProject () {
    return axios({
      method: 'post',
      url: '/taskPublish/getProject'
      // data: params
    })
  },
  // 
  getTaskGroupTree (params) {
    return axios({
      method: 'post',
      url: '/taskSet/getTaskGroupTree',
      data: params
    })
  },
  // 
  getPublishTaskList (params) {
    return axios({
      method: 'post',
      url: '/taskPublish/getPublishTaskList',
      data: params
    })
  }

}
export default taskIssue



